import Worker from './thread.1.worker.js';



const empty_array = [];



export default class Thread
{
	constructor
	({
		wasm_wrapper,

		function_name,

		data = empty_array,

		js_loop = false,

		freeWorkedMemory = false,

		try_wrap = false,
	})
	{
		this.wasm_wrapper = wasm_wrapper;

		this.stack_addr = this.wasm_wrapper.exports.RDTY_WASM_WRAPPER_malloc(this.wasm_wrapper.options.thread_stack_size);

		this.stack_pointer = this.stack_addr + this.wasm_wrapper.options.thread_stack_size;

		// Should be private?
		this.promise =
			new Promise
			(
				resolve =>
				{
					this.worker = new Worker();

					this.worker.onmessage = resolve;

					const { code, memory, imports } = wasm_wrapper;

					// for (const key in memory)
					// {
					// 	if (key !== 'buffer')
					// 	{
					// 		delete memory[key];
					// 	}
					// }

					const { stack_pointer } = this;

					this.worker.postMessage
					({
						code,
						memory,
						imports,
						stack_pointer,

						function_name,
						data,
						try_wrap,
						js_loop,
						freeWorkedMemory,
					});
				},
			);
	}

	terminate ()
	{
		this.worker.terminate();

		this.wasm_wrapper.exports.RDTY_WASM_WRAPPER_free(this.stack_addr);

		// this.promise = null;
	}

	async join ()
	{
		await this.promise;

		this.terminate();
	}
}
