import '@babel/runtime/regenerator';

import WasmWrapper from '../../wasm-wrapper';



const wasm = new WasmWrapper();



onmessage =
	async ({ data }) =>
	{
		const [ function_addr, thread_types_addr, thread_values_addr ] = data.data;

		await wasm.init(data);

		const _function = wasm.Function(function_addr);

		const arg_types = wasm.Size(thread_types_addr, _function.length);

		const args = new Array(_function.length);

		const any_type_size = wasm.Size(wasm.exports.RDTY_WASM_WRAPPER_ANYVALUE_SIZE)[0];

		for (let i = 0; i < _function.length; ++i)
		{
			args[i] = wasm.interp[arg_types[i]](thread_values_addr + (any_type_size * i))[0];
		}

		_function(...args);

		postMessage(true);
	};
