const _call =
	(wasm, message_data) =>
	{
		if (message_data.js_loop)
		{
			setInterval(wasm.exports[message_data.function_name], 0, ...message_data.data);
		}
		else
		{
			wasm.exports[message_data.function_name](...message_data.data);
		}
	};

export default (wasm, message_data) =>
{
	if (message_data.try_wrap)
	{
		try
		{
			_call(wasm, message_data);
		}
		catch (evt)
		{
			console.log(evt);
		}
	}
	else
	{
		_call(wasm, message_data);
	}
};
