import '@babel/runtime/regenerator';

import WasmWrapper from '../../wasm-wrapper';
import call from '../common/call';



const wasm = new WasmWrapper();



onmessage =
	async ({ data }) =>
	{
		if (data.work)
		{
			call(wasm, data);
		}
		else
		{
			await wasm.init(data);
		}

		postMessage(true);
	};
