/**
 * Using TypedArray.subarray() is preferred
 * when accessing to data
 * to avoid extra memory allocation.
 *
 * Strange std::string behavior:
 * if std::string data length <=11, std::string object address is the same with its data;
 * if >11, std::string object name stores address of beginning of the data.
 * So in second case one can use WasmWrapper::CString method to get string bytes.
 * Maybe it's not related to data length, but to dynamic memory allocation.
 *
 *
 *
 * If memory is shared then memory growing is not allowed
 * and maximum memory size is specified at compilation.
 *
 *
 *
 * Passing argument by reference works as passing by pointer.
 * So, functions with reference parameters expect address instead of value.
 *
 *
 *
 * TODO: determination capabiity of what wasm memory type is being used.
 */



// Webpack arraybuffer-loader is required.
// import test_simd_wasm_buffer from './test-simd/build/clang-wasm32/output/wasm/test-simd.wasm';



const __CODE_TYPE_BYTECODE__ = 0;
const __CODE_TYPE_FUNCTION__ = 1;



const IDLE_FUNCTION = () => 0;

const DEBUG_LOG = console.log.bind(console);
// const DEBUG_LOG = (...content) => console.log(...content.map(elm => `%c${ elm }`), 'background-color: #00858A; padding: 2px;');

const DEBUG_INFO = [];

const DEBUG_INFO_PUSH = (content) => DEBUG_INFO.push(content);

const DEBUG_LABEL_LOG = () =>
{
	DEBUG_INFO.forEach(label => console.log(`%c${ label }`, 'background-color: #00858A; padding: 2px;'));

	DEBUG_INFO.length = 0;
};

const MISSED_IMPORT_FUNCTION_LOG = (content) => console.log(`%c${ content }`, 'background-color: red; padding: 2px;');



class WasmWrapper
{
	static text_decoder = new TextDecoder('utf-8');
	static text_encoder = new TextEncoder();

	// static convertUint8ArrayToDomString (uint8_array)
	// {
	// 	return WasmWrapper.text_decoder.decode(uint8_array);
	// }

	// Version for shared buffer.
	// Decoding views of shared buffer is not allowed.
	static convertUint8ArrayToDomString (uint8_array)
	{
		return WasmWrapper.text_decoder.decode(uint8_array.slice());
	}

	static convertDomStringToUint8Array (text)
	{
		return WasmWrapper.text_encoder.encode(text);
	}

	static async testSimd ()
	{
		let result = false;

		try
		{
			// webpack arraybuffer-loader has to be installed when using the library with soures files.
			const test_simd_wasm_buffer = (await import('./test-simd/build/clang-wasm32/output/wasm/test-simd.wasm')).default;

			const wasm_module = await WebAssembly.compile(test_simd_wasm_buffer);

			// LOG(WebAssembly.Module.exports(wasm_module))
			// LOG(WebAssembly.Module.imports(wasm_module))

			await WebAssembly.instantiate
			(
				wasm_module,

				{
					env:
					{
						memory:
						(
							new WebAssembly.Memory
							({
								initial: 2,
								maximum: 2,
							})
						),
					},
				},
			);

			result = true;
		}
		catch (_) {}

		DEBUG_INFO_PUSH(`WASM SIMD: ${ result ? 'ON' : 'OFF' }`);

		return result;
	}

	constructor (options = {})
	{
		const wasm_wrapper = this;



		this.options =
			Object.assign
			(
				{
					// 1024 b * 1024 b == 1048576 b == 1 mb
					thread_stack_size: 0x0100000,
					// thread_stack_size: 1048576 * 4,
				},

				options,
			);



		// TODO: reorder.
		// TODO: order type related objects the same.
		this.ADDR_SIZE = 0;
		this.SIZE_SIZE = 0;
		this.UINT32_SIZE = 0;
		this.FLOAT_SIZE = 0;
		this.DOUBLE_SIZE = 0;
		this.INT_SIZE = 0;

		this.memory_views =
		{
			UI8: null,
			I8: null,
			UI16: null,
			I16: null,
			UI32: null,
			I32: null,
			// TODO: add 64 bit types.
			F32: null,
			F64: null,
		};

		this.interp =
		[
			(val, size) => wasm_wrapper.Int(val, size),
			(val, size) => wasm_wrapper.Size(val, size),
			(val, size) => wasm_wrapper.Float(val, size),
			(val, size) => wasm_wrapper.Double(val, size),

			// const char*
			(val, size) => wasm_wrapper.Addr(val, size),

			// void*
			(val, size) => wasm_wrapper.Addr(val, size),
		];

		this.__heap_pointer_init = 0;



		const custom_functions =
		{
			console_log: (size, types_addr, values_addr) =>
			{
				const arg_types = this.Size(types_addr, size);

				const args = new Array(size);

				// const any_type_size = this.Size(this.exports.RDTY_WASM_WRAPPER_ANYVALUE_SIZE)[0];
				const any_type_size = this.Size(this.exports.__getAnyvalueSize__())[0];

				for (let i = 0; i < size; ++i)
				{
					// const char*
					if (arg_types[i] === 4)
					{
						args[i] = this.CString2(this.Addr(values_addr + (any_type_size * i))[0]);
					}
					// std::string
					else if (arg_types[i] === 6)
					{
						args[i] = WasmWrapper.convertUint8ArrayToDomString(this.StdString(values_addr + (any_type_size * i)));
					}
					else
					{
						args[i] = this.interp[arg_types[i]](values_addr + (any_type_size * i))[0];
					}
				}

				console.log('%cWASM:', 'color: #bada55', ...args);
			},

			console_log2: (...value) =>
			{
				console.log('%cWASM:', 'color: #bada55', ...value);
			},

			date_now: () => Date.now(),

			js_loop: (function_addr, ...args) =>
			{
				const _function = this.Function(function_addr);

				// setInterval(_function, 1000, ...args);
				setInterval(_function, 0, ...args);
			},

			rdty_sleep: (duration) =>
			{
				// Global state for running the program.

				const DATA_ADDR = 16;

				if (!this.sleeping)
				{
					// We are called in order to start a sleep/unwind.
					// console.log('sleep...');
					// Fill in the data structure. The first value has the stack location,
					// which for simplicity we can start right after the data structure itself.
					this.memory_views.I32[DATA_ADDR >> 2] = DATA_ADDR + 8;
					// The end of the stack will not be reached here anyhow.

					// TODO: use stack_size
					this.memory_views.I32[DATA_ADDR + 4 >> 2] = 1024 * 1024;
					this.exports.asyncify_start_unwind(DATA_ADDR);
					this.sleeping = true;

					// Resume after the proper delay.
					setTimeout
					(
						() =>
						{
							// console.log('timeout ended, starting to rewind the stack');
							this.exports.asyncify_start_rewind(DATA_ADDR);
							// The code is now ready to rewind; to start the process, enter the
							// first function that should be on the call stack.
						},

						duration,
					);
				}
				else
				{
					// We are called as part of a resume/rewind. Stop sleeping.
					// console.log('...resume');
					this.exports.asyncify_stop_rewind();
					this.sleeping = false;
				}
			},
		};

		// If this is main worker.
		if (self.window && self.SharedArrayBuffer)
		{
			Object.assign
			(
				custom_functions,

				{
					runThread: (function_addr, thread_addr, thread_types_addr, thread_values_addr) =>
					{
						if (!this.threads)
						{
							this.threads = {};
						}

						const thread =
							new WasmWrapper.Thread2
							(
								this,

								[
									function_addr,
									this.exports.RDTY_WASM_WRAPPER_getStdVectorData(thread_types_addr),
									this.exports.RDTY_WASM_WRAPPER_getStdVectorData(thread_values_addr)
								],
							);

						this.threads[thread_addr] = thread;
					},

					joinThread: (thread_addr) =>
					{
						this.async_count = this.async_count || 0;

						++this.async_count;

						if (this.async_count === 1)
						{
							this.async_promise =
								new Promise
								(
									resolve =>
									{
										this.async_promise_setter = (val) =>
										{
											if (val)
											{
												resolve();
											}
										};
									},
								);
						}

						const DATA_ADDR = 16;

						if (!this.sleeping)
						{
							this.memory_views.I32[DATA_ADDR >> 2] = DATA_ADDR + 8;
							this.memory_views.I32[DATA_ADDR + 4 >> 2] = 1024 * 1024;

							this.exports.asyncify_start_unwind(DATA_ADDR);

							this.sleeping = true;

							this.threads[thread_addr].promise
								.then
								(
									() =>
									{
										this.exports.asyncify_start_rewind(DATA_ADDR);

										this.async_func(...this.async_args);

										if (this.async_count % 2 === 0)
										{
											this.async_promise_setter(true);
										}
									}
								);
						}
						else
						{
							this.exports.asyncify_stop_rewind();

							this.sleeping = false;
						}
					},

					terminateThread: (thread_addr) =>
					{
						this.threads[thread_addr].terminate();
					},
				},
			);
		}

		// "malloc" and "free" WASI-libc functions are not thread-safe
		// since they operate with the same heap. To be able to use these functions
		// in multiple threads we need to specify them as imports and remove the part of
		// WASI-libc where they are defined (dlmalloc.o):
		// ${ DIR_WASI }/bin/llvm-ar -d ${ DIR_WASI }/share/wasi-sysroot/lib/wasm32-wasi/libc.a dlmalloc.o
		{
			const LOCKED = 0;
			const UNLOCKED = 1;

			let malloc_lock = IDLE_FUNCTION;
			let malloc_unlock = IDLE_FUNCTION;

			if (!self.window)
			{
				malloc_lock = () =>
				{
					const __atomic_malloc_index = this.exports.__getAtomicMalloc__() / 4;
					// const __atomic_malloc_index = this.exports.__atomic_malloc / 4;

					while (true)
					{
						const old_state =
							Atomics.compareExchange
							(
								this.memory_views.I32,
								__atomic_malloc_index,
								/* old state >>> */ UNLOCKED,
								/* new state >>> */ LOCKED,
							);

						if (old_state === UNLOCKED)
						{
							return;
						}

						Atomics.wait(this.memory_views.I32, __atomic_malloc_index, LOCKED);
					}

					// Atomics.wait(this.memory_views.I32, __atomic_malloc_index, LOCKED);
					// Atomics.store(this.memory_views.I32, __atomic_malloc_index, LOCKED);
				};

				malloc_unlock = () =>
				{
					const __atomic_malloc_index = this.exports.__getAtomicMalloc__() / 4;
					// const __atomic_malloc_index = this.exports.__atomic_malloc / 4;

					Atomics.store(this.memory_views.I32, __atomic_malloc_index, UNLOCKED);
					// Atomics.notify(this.memory_views.I32, __atomic_malloc_index);
					Atomics.notify(this.memory_views.I32, __atomic_malloc_index, 1);
				};
			}

			Object.assign
			(
				custom_functions,

				{
					// TODO: port to C++ ?
					malloc: (size) =>
					{
						const _size = Math.max(16, Math.pow(2, Math.ceil(Math.log(size) / Math.log(2))));

						const position = Math.log2(_size);

						let addr = 0;

						malloc_lock();
						// this.__lock__(this.exports.__getAtomicMalloc__());
						// if (this.__id__) this.Size(this.exports.__malloc_test, 2)[0] = this.__id__;

						if (this.__heap_segments[position])
						{
							// LOG(this.__heap_segments, this.__heap_segments[position])
							addr = this.__heap_segments[position];

							this.__heap_segments[position] = 0;

							// this.Size(addr - 16)[0] = _size;
						}
						else
						{
							const __heap_pointer = this.Size(this.exports.__getHeapPointer__());
							// const __heap_pointer = this.Size(this.exports.__heap_pointer);

							this.Size(__heap_pointer[0])[0] = _size;

							__heap_pointer[0] += 16;

							addr = __heap_pointer[0];

							__heap_pointer[0] += _size;
						}

						// if (this.__id__) this.Size(this.exports.__malloc_test, 2)[1] = this.__id__;
						// if (this.__id__ && this.Size(this.exports.__malloc_test, 2)[0] !== this.Size(this.exports.__malloc_test, 2)[1]) LOG(...this.Size(this.exports.__malloc_test, 2))
						malloc_unlock();
						// this.__unlock__(this.exports.__getAtomicMalloc__());

						return addr;
					},

					free: (addr) =>
					{
						const size = this.Size(addr - 16)[0];

						malloc_lock();
						// this.__lock__(this.exports.__getAtomicMalloc__());
						// if (this.__id__) this.Size(this.exports.__malloc_test, 2)[0] = this.__id__;

						const __heap_pointer = this.Size(this.exports.__getHeapPointer__());
						// const __heap_pointer = this.Size(this.exports.__heap_pointer);

						if (__heap_pointer[0] === addr + size)
						{
							__heap_pointer[0] = addr - 16;
						}
						else
						{
							this.__heap_segments[Math.log2(size)] = addr;
						}

						// if (this.__id__) this.Size(this.exports.__malloc_test, 2)[1] = this.__id__;
						// if (this.__id__ && this.Size(this.exports.__malloc_test, 2)[0] !== this.Size(this.exports.__malloc_test, 2)[1]) LOG(...this.Size(this.exports.__malloc_test, 2))
						malloc_unlock();
						// this.__unlock__(this.exports.__getAtomicMalloc__());
					},

					realloc: () => IDLE_FUNCTION,
					calloc: () => IDLE_FUNCTION,

					__libc_malloc: () => IDLE_FUNCTION,
					__libc_realloc: () => IDLE_FUNCTION,
					__libc_calloc: () => IDLE_FUNCTION,
					__libc_free: () => IDLE_FUNCTION,

					posix_memalign: () => IDLE_FUNCTION,

					// realloc: () => LOG("realloc"),
					// calloc: () => LOG("calloc"),

					// __libc_malloc: () => LOG("__libc_malloc"),
					// __libc_realloc: () => LOG("__libc_realloc"),
					// __libc_calloc: () => LOG("__libc_calloc"),
					// __libc_free: () => LOG("__libc_free"),

					// posix_memalign: () => LOG("posix_memalign"),
				},
			);
		}

		this.custom_functions = custom_functions;



		class DataBase
		{
			constructor (size, type, type_size)
			{
				if (typeof size === 'object')
				{
					this.size = size.length;
				}
				else
				{
					this.size = size;
				}

				this.addr = wasm_wrapper.exports.RDTY_WASM_WRAPPER_malloc(this.size * type_size);

				this.data = wasm_wrapper[type](this.addr, this.size);

				if (typeof size === 'object')
				{
					this.set(size);
				}
			}

			set (_data, offset = 0)
			{
				// LOG('_data', _data)
				this.data.set(_data, offset);
			}

			free ()
			{
				wasm_wrapper.exports.RDTY_WASM_WRAPPER_free(this.addr);
			}
		}

		class DataFloat extends DataBase
		{
			constructor (size)
			{
				super(size, 'Float', wasm_wrapper.FLOAT_SIZE);
			}
		}

		this.DataFloat = DataFloat;

		class DataUint32 extends DataBase
		{
			constructor (size)
			{
				super(size, 'Uint32', wasm_wrapper.UINT32_SIZE);
			}
		}

		this.DataUint32 = DataUint32;




		class ThreadPromiseBunch
		{
			constructor ()
			{
				this.running_count = 0;
			}

			async run (callback1, callback2 = IDLE_FUNCTION)
			{
				++this.running_count;

				if (this.running_count > 1)
				{
					return;
				}

				// LOG('ThreadPromiseBunch start __heap_pointer', wasm_wrapper.Size(wasm_wrapper.exports.__getHeapPointer__())[0])

				return Promise.all(await callback1())
					.then
					(() =>
					{
						// LOG('ThreadPromiseBunch end __heap_pointer', wasm_wrapper.Size(wasm_wrapper.exports.__getHeapPointer__())[0])

						wasm_wrapper.resetHeapPointer();

						callback2();

						if (this.running_count > 1)
						{
							this.running_count = 0;

							this.run(callback1, callback2);
						}
						else
						{
							this.running_count = 0;
						}
					});
			}
		}

		this.ThreadPromiseBunch = ThreadPromiseBunch;
	}

	Char (addr, length = 1)
	{
		return this.memory_views.UI8.subarray(addr, addr + length);
	}

	Uint8 (addr, length = 1)
	{
		return this.memory_views.UI8.subarray(addr, addr + length);
	}

	Int (addr, length = 1)
	{
		const _addr = addr / this.INT_SIZE;

		return this.memory_views.I32.subarray(_addr, _addr + length);
	}

	Uint32 (addr, length = 1)
	{
		const _addr = addr / this.UINT32_SIZE;

		return this.memory_views.UI32.subarray(_addr, _addr + length);
	}

	Size (addr, length = 1)
	{
		const _addr = addr / this.SIZE_SIZE;

		return this.memory_views.UI32.subarray(_addr, _addr + length);
	}

	Float (addr, length = 1)
	{
		const _addr = addr / this.FLOAT_SIZE;

		return this.memory_views.F32.subarray(_addr, _addr + length);
	}

	Double (addr, length = 1)
	{
		// Data must be aligned with 8 bytes.
		const _addr = addr / this.DOUBLE_SIZE;

		return this.memory_views.F64.subarray(_addr, _addr + length);
	}

	Addr (addr, length = 1)
	{
		const _addr = addr / this.ADDR_SIZE;

		return this.memory_views.UI32.subarray(_addr, _addr + length);
	}

	Addr2 (name, length = 1)
	{
		return this.Addr(this.exports[name].value, length);
	}

	Function (addr)
	{
		return this.exports.__indirect_function_table.get(addr);
	}

	CStringLen (addr)
	{
		const _addr = addr;

		for (let vend = 0; ; ++vend)
		{
			if (this.Char(_addr + vend)[0] === 0)
			{
				return vend;
			}
		}
	}

	CString (addr)
	{
		return this.memory_views.UI8.subarray(addr, addr + this.CStringLen(addr));
	}

	CString2 (addr)
	{
		return WasmWrapper.convertUint8ArrayToDomString(this.memory_views.UI8.subarray(addr, addr + this.CStringLen(addr)));
	}

	StdString (addr)
	{
		const result =
			this.Char
			(
				this.exports.RDTY_WASM_WRAPPER_getStdStringData(addr),

				this.exports.RDTY_WASM_WRAPPER_getStdStringSize(addr),
			);

		return result;
	}

	StdVector (addr, type)
	{
		const result =
			this[type]
			(
				this.exports.RDTY_WASM_WRAPPER_getStdVectorData(addr),

				this.exports.RDTY_WASM_WRAPPER_getStdVectorSize(addr),
			);

		return result;
	}

	StdVectorUint8 (addr)
	{
		const result =
			this.Uint8
			(
				this.exports.RDTY_WASM_WRAPPER_getStdVectorData(addr),

				this.exports.RDTY_WASM_WRAPPER_getStdVectorSize(addr),
			);

		return result;
	}

	StdVectorSize (addr)
	{
		const result =
			this.Size
			(
				this.exports.RDTY_WASM_WRAPPER_getStdVectorData(addr),

				this.exports.RDTY_WASM_WRAPPER_getStdVectorSize(addr),
			);

		return result;
	}

	StdVectorUint32 (addr)
	{
		const result =
			this.Uint32
			(
				this.exports.RDTY_WASM_WRAPPER_getStdVectorData(addr),

				this.exports.RDTY_WASM_WRAPPER_getStdVectorSize(addr),
			);

		return result;
	}

	StdVectorFloat (addr)
	{
		const result =
			this.Float
			(
				this.exports.RDTY_WASM_WRAPPER_getStdVectorData(addr),

				this.exports.RDTY_WASM_WRAPPER_getStdVectorSize(addr),
			);

		return result;
	}

	StdVectorAddr (addr)
	{
		const result =
			this.Addr
			(
				this.exports.RDTY_WASM_WRAPPER_getStdVectorData(addr),

				this.exports.RDTY_WASM_WRAPPER_getStdVectorSize(addr),
			);

		return result;
	}

	Class (name)
	{
		const wasm_wrapper = this;



		class _Class
		{
			static name = name;

			static overloaded = {};



			constructor (input)
			{
				if (typeof input === 'number')
				{
					this.addr = input;
				}
				else if (typeof input === 'string')
				{
					[ this.addr ] = wasm_wrapper.Addr2(input);
				}
			}
		}

		self.Object.keys(this.exports_demangled)
			.filter
			(
				member_name => member_name.includes(name),
			)
			.forEach
			(
				member_name =>
				{
					let member_name_trimmed = member_name.replace(`${ name }::`, '').replace(/\((.*)+/g, '');

					// Some objects may be exported twice from wasm. TODO: why does it occur?
					const wasm_object =
						wasm_wrapper.exports_demangled[member_name][0] || wasm_wrapper.exports_demangled[member_name];

					if (typeof wasm_object === 'function')
					{
						const parameters =
							member_name.match(/\((.*)+/g)?.[0].replace(/\(|( )|\)/g, '').split(',');

						if (parameters.length && parameters[0] === '')
						{
							parameters.length = 0;
						}

						// static function
						if (parameters.length === wasm_object.length)
						{
							if (_Class[member_name_trimmed])
							{
								if (!_Class.overloaded[member_name_trimmed])
								{
									_Class.overloaded[member_name_trimmed] = 0;
								}

								++_Class.overloaded[member_name_trimmed];

								member_name_trimmed += `_o${ _Class.overloaded[member_name_trimmed] }`;
							}

							_Class[member_name_trimmed] = wasm_object;
						}

						// non-static function
						else
						{
							if (_Class.prototype[member_name_trimmed])
							{
								if (!_Class.overloaded[member_name_trimmed])
								{
									_Class.overloaded[member_name_trimmed] = 0;
								}

								++_Class.overloaded[member_name_trimmed];

								member_name_trimmed += `_o${ _Class.overloaded[member_name_trimmed] }`;
							}

							// Non-wrapped
							_Class[`proto_${ member_name_trimmed }`] = wasm_object;

							_Class.prototype[member_name_trimmed] =
								function (...args)
								{
									return wasm_object(this.addr, ...args);
								};
						}
					}
					else if (typeof wasm_object === 'object')
					{
						_Class[member_name_trimmed] = wasm_object;
					}
				},
			);

		// Presume New() returns a pointer
		if (_Class.New)
		{
			_Class.NewWrapper = () => new _Class(_Class.New());
		}



		return _Class;
	}

	// TODO: Mutex.

	resizeStdVector (addr, size)
	{
		this.exports.RDTY_WASM_WRAPPER_StdVector_resize(addr, size);
	}

	updateStdVectorData (addr, type, _data)
	{
		// this.exports.RDTY_WASM_WRAPPER_StdVector_resize(addr, _data.length);

		this.StdVector(addr, type).set(_data);
	}

	// demangle (name, name_addr)
	// {
	// 	this.memory_views.UI8.set(WasmWrapper.convertDomStringToUint8Array(name), name_addr);

	// 	const demangled_name =
	// 		WasmWrapper.convertUint8ArrayToDomString
	// 		(this.CString(this.exports.RDTY_WASM_WRAPPER_demangleCxxName(name_addr)))
	// 			.replace(/, /g, ',');

	// 	return demangled_name;
	// }

	initHeapPointer ()
	{
		[ this.__heap_pointer_init ] = this.Size(this.exports.__getHeapPointer__());
		// [ this.__heap_pointer_init ] = this.Size(this.exports.__heap_pointer);

		this.__heap_segments.fill(0);
	}

	resetHeapPointer ()
	{
		this.Size(this.exports.__getHeapPointer__())[0] = this.__heap_pointer_init;
		// this.Size(this.exports.__heap_pointer)[0] = this.__heap_pointer_init;

		this.__heap_segments.fill(0);
	}

	demangleCxxNames (debug)
	{
		const demangled_name_max_length = 1024;
		const demangled_name_addr = this.exports.RDTY_WASM_WRAPPER_malloc(demangled_name_max_length);

		this.exports_demangled_reduced = {};

		this.exports_demangled =
			Object.keys(this.exports)
				.reduce
				(
					(exports_demangled, _name) =>
					{
						if (_name.startsWith('_Z'))
						{
							const name = `${ _name }\0`;

							// const demangled_name = this.demangle(name, demangled_name_addr);

							this.memory_views.UI8.set(WasmWrapper.convertDomStringToUint8Array(name), demangled_name_addr);

							const demangled_name =
								WasmWrapper.convertUint8ArrayToDomString
								(this.CString(this.exports.RDTY_WASM_WRAPPER_demangleCxxName(demangled_name_addr)))
									.replace(/, /g, ',');

							if (exports_demangled[demangled_name])
							{
								if (Array.isArray(exports_demangled[demangled_name]))
								{
									exports_demangled[demangled_name].push(this.exports[_name]);

									return exports_demangled;
								}

								const first_entry = exports_demangled[demangled_name];

								exports_demangled[demangled_name] = [ first_entry, this.exports[_name] ];

								return exports_demangled;
							}

							exports_demangled[demangled_name] = this.exports[_name];

							this.exports_demangled_reduced[demangled_name.replace(/\([^)]*\)/g, '')] = this.exports[_name];
						}

						return exports_demangled;
					},

					{},
				);

		this.exports.RDTY_WASM_WRAPPER_free(demangled_name_addr);

		if (debug)
		{
			DEBUG_LOG(this.exports_demangled);
			DEBUG_LOG(this.exports_demangled_reduced);
		}
	}

	doCommons (buffer, debug)
	{
		this.memory_views.UI8 = new Uint8Array(buffer);
		this.memory_views.I8 = new Int8Array(buffer);
		this.memory_views.UI16 = new Uint16Array(buffer);
		this.memory_views.I16 = new Int16Array(buffer);
		this.memory_views.UI32 = new Uint32Array(buffer);
		this.memory_views.I32 = new Int32Array(buffer);
		this.memory_views.F32 = new Float32Array(buffer);
		this.memory_views.F64 = new Float64Array(buffer);

		if (this.exports.RDTY_WASM_WRAPPER_sizeof)
		{
			this.ADDR_SIZE = this.exports.RDTY_WASM_WRAPPER_sizeof(0);
			this.SIZE_SIZE = this.exports.RDTY_WASM_WRAPPER_sizeof(1);
			this.UINT32_SIZE = this.exports.RDTY_WASM_WRAPPER_sizeof(2);
			this.FLOAT_SIZE = this.exports.RDTY_WASM_WRAPPER_sizeof(3);
			this.DOUBLE_SIZE = this.exports.RDTY_WASM_WRAPPER_sizeof(4);
			this.INT_SIZE = this.exports.RDTY_WASM_WRAPPER_sizeof(5);

			this.SIMD = this.Size(this.exports.__getSimd__())[0];
		}
	}

	async async (_function, ...args)
	{
		this.async_args = args;

		(this.async_func = _function)(...args);

		this.exports.asyncify_stop_unwind();

		await this.async_promise;

		this.async_count = 0;
	}

	async init
	({
		// TODO: reorder.
		code,
		memory = null,
		imports = { env: {} },
		stack_pointer = undefined,
		doCommons = true,
		forceLocalMemory = false,
		memory_params = { minimum: 65536, maximum: 65536, initial: 65536 },
		demangleCxxNames = false,
		debug = false,
		// malloc = true,
	})
	{
		// const wasm_wrapper = this;

		let code_type = null;

		// Code for passing to other WASM instances (e.g. threads).
		this.code = code;

		if (typeof code === 'string')
		{
			if (code.slice(0, 8) === 'function')
			{
				code_type = __CODE_TYPE_FUNCTION__;

				code = new Function(code);

				DEBUG_INFO_PUSH(`WASM CODE: string function -> ${ code.constructor.name }`);
			}
			// base64
			else
			{
				code = Uint8Array.from(atob(code), c => c.charCodeAt(0));

				// TODO: Make code shared (if can) to speed up passing it to workers. DONE?
				if (self.SharedArrayBuffer)
				{
					const code_temp = code;

					code = new Uint8Array(new SharedArrayBuffer(code.length));

					code.set(code_temp);
				}

				this.code = code;

				code_type = __CODE_TYPE_BYTECODE__;

				DEBUG_INFO_PUSH(`WASM CODE: string base64 -> ${ code.constructor.name }`);
			}
		}
		else if (typeof code === 'function')
		{
			code_type = __CODE_TYPE_FUNCTION__;

			this.code = code.toString();

			DEBUG_INFO_PUSH(`WASM CODE: function`);
		}
		// Code is an Uint8Array backed (SharedArrayBuffer optionally) or ArrayBuffer or SharedArrayBuffer.
		// UPD. SharedArrayBuffer can not be used as source for WebAssembly module, only for Uint8Array
		// backed by SharedArrayBuffer.
		else
		{
			code_type = __CODE_TYPE_BYTECODE__;

			DEBUG_INFO_PUSH(`WASM CODE: ${ code.constructor.name }`);
		}

		DEBUG_INFO_PUSH(`WASM CODE: ${ code_type === __CODE_TYPE_BYTECODE__ ? 'BYTECODE' : 'FUNCTION' }`);

		// DEBUG_LABEL_LOG();



		if (code_type === __CODE_TYPE_BYTECODE__)
		{
			const wasm_module = await WebAssembly.compile(code);

			if (debug)
			{
				DEBUG_LOG(wasm_module);
			}

			const wasm_module_imports = WebAssembly.Module.imports(wasm_module);
			// const wasm_module_exports = WebAssembly.Module.exports(wasm_module);

			this.memory = null;

			// Memory comes from WASM main worker instance to thread instance.
			// is always imported and shared.
			if (memory)
			{
				this.memory = memory;

				DEBUG_INFO_PUSH('WASM MEMORY: IMPORTED')
				DEBUG_INFO_PUSH('WASM MEMORY: SHARED')
			}
			else
			{
				const [ imported_memory ] =
					wasm_module_imports
						.filter((import_desc) => (import_desc.kind === 'memory'));

				const shared = Boolean(self.SharedArrayBuffer) && !forceLocalMemory;

				if (imported_memory)
				{
					DEBUG_INFO_PUSH('WASM MEMORY: IMPORTED')

					// chrome://flags#enable-experimental-webassembly-features must be enabled
					// to access "type" property of "imported_memory". It contains memory
					// type and size info produced by the linker.
					// "--import-memory" linker flag specifies whether memory is imported to WebAssembly module.
					// "--shared-memory" linker flag specifies whether memory is shared between WebAssembly instances.
					// "--max-memory=N" linker flag specifies maximum size of memory for WebAssembly instance.
					if (imported_memory.type)
					{
						const memory_desc =
						{
							initial: imported_memory.type.maximum || imported_memory.type.minimum,
							maximum: imported_memory.type.maximum || imported_memory.type.minimum,
							shared: imported_memory.type.shared && shared,
						};

						this.memory = new WebAssembly.Memory(memory_desc);

						DEBUG_INFO_PUSH(`WASM MEMORY: ${ memory_desc.shared ? 'SHARED' : 'NOT SHARED' }`)
					}
					// Parameters must match values produced by the linker.
					else
					{
						this.memory =
							new WebAssembly.Memory
							({
								// 65536 pages == 4gb, maximum possible memory size for 32-bit WebAssembly instance.
								initial: memory_params.initial,
								maximum: memory_params.maximum, // Must match "--max-memory=N" flag.
								shared, // Must match "--shared-memory" flag.
							});

						DEBUG_INFO_PUSH(`WASM MEMORY: ${ shared ? 'SHARED' : 'NOT SHARED' }`)
					}
				}
				else
				{
					DEBUG_INFO_PUSH('WASM MEMORY: EXPORTED')

					// // In this case arbitrary parameters may be used.
					// this.memory =
					// 	new WebAssembly.Memory
					// 	({
					// 		// 65536 pages == 4gb, maximum possible memory size for 32-bit WebAssembly instance.
					// 		initial: memory_params.initial,
					// 		maximum: memory_params.maximum,
					// 		shared,
					// 	});
				}
			}

			// Object for passing to threads.
			this.imports = { env: {} };

			const import_modules = {};

			Object.keys(imports)
				.forEach
				(
					module =>
					{
						Object.keys(imports[module])
							.forEach
							(
								_import =>
								{
									if (!this.imports[module])
									{
										this.imports[module] = {};
									}

									this.imports[module][_import] = 0;

									imports[module][_import] = imports[module][_import] || IDLE_FUNCTION;
								}
							);
					},
				);

			Object.assign(import_modules, imports);

			Object.assign
			(
				import_modules.env,

				this.custom_functions
			);

			// Imported memory
			if (this.memory)
			{
				import_modules.env.memory = this.memory;
			}

			wasm_module_imports
				.forEach
				(
					_import =>
					{
						if (!import_modules[_import.module])
						{
							import_modules[_import.module] = {};
						}

						if (!import_modules[_import.module][_import.name])
						{
							if (debug)
							{
								DEBUG_LOG(`Missed ${ _import.module } import:`, _import.name);
							}

							import_modules[_import.module][_import.name] = IDLE_FUNCTION;
							// import_modules[_import.module][_import.name] = () => { MISSED_IMPORT_FUNCTION_LOG(`Missed import function called: module "${ _import.module }", function "${ _import.name }"`); throw new Error('QWE'); };
						}
					},
				);

			const wasm_module_instance =
				await WebAssembly.instantiate
				// await WebAssembly.instantiateStreaming
				(
					wasm_module,

					import_modules,
				);

			// Exported memory
			if (!this.memory)
			{
				this.memory = wasm_module_instance.exports.memory;
			}

			if (debug)
			{
				DEBUG_LOG(wasm_module_instance);
			}

			// imported || exported
			// const { buffer } = this.memory || wasm_module_instance.exports.memory;

			// this.instance = wasm_module_instance;

			this.exports = wasm_module_instance.exports;
		}
		/**
		 * Initializes WasmWrapper instance with JS code compiled from WASM with wasm2js.
		 *
		 * wasm2js generates code with Atomics, even if its input WASM is compiled without flag "--shared-memory".
		 * TODO: use --disable-threads to generating Atomics as --help says.
		 * UPD: There are still Atomics even with --disable-threads flag.
		 *
		 * if (!self.Atomics)
		 * {
		 *   self.Atomics = { load: (arr, index) => arr[index] };
		 * }
		 */
		else
		{
			/**
			 * Binary search for a max value without knowing the exact value, only that it
			 * can be under or over It dose not test every number but instead looks for
			 * 1,2,4,8,16,32,64,128,96,95 to figure out that you thought about #96 from
			 * 0-infinity
			 *
			 * @example findFirstPositive(x => matchMedia(`(max-resolution: ${x}dpi)`).matches)
			 * @author Jimmy Wrting
			 * @see {@link https://stackoverflow.com/a/72124984/1008999}
			 * @param {function} f       The function to run the test on (should return truthy or falsy values)
			 * @param {bigint} [b=1]  Where to start looking from
			 * @param {function} d privately used to calculate the next value to test
			 * @returns {bigint} Intenger
			 */
			// function findFirstPositive (f,b=1n,d=(e,g,c)=>g<e?-1:0<f(c=e+g>>1n)?c==e||0>=f(c-1n)?c:d(e,c-1n):d(c+1n,g)) {
			// 	for (;0>=f(b);b<<=1n);return d(b>>1n,b)-1n
			// }

			// const tries = []
			// const maxSize = findFirstPositive(x => {
			// 	tries.push(Number(x).toLocaleString())
			// 	try { new ArrayBuffer(Number(x)); return false } catch { return true }
			// })
			// console.log('found it in', tries.length, 'attempts')
			// console.log(Number(maxSize))
			// console.log(tries)

			// TODO: find maximum possible size of ArrayBuffer.
			// this.memory = memory || { buffer: new (self.SharedArrayBuffer || ArrayBuffer)(2145386496) };

			this.memory = null;

			if (memory)
			{
				this.memory = memory;
			}
			// else if (forceLocalMemory || !self.SharedArrayBuffer)
			// {
			// 	this.memory = { buffer: new self.ArrayBuffer(2145386496) };
			// }
			// else
			// {
			// 	this.memory = { buffer: new self.SharedArrayBuffer(2145386496) };
			// }
			else
			{
				this.memory = { buffer: new self.ArrayBuffer(2145386496) };
			}

			const imports_env =
				Object.assign
				(
					imports.env,

					{ memory: this.memory },

					this.custom_functions
				);

			this.exports = code(imports_env);
		}



		if (stack_pointer !== undefined)
		{
			this.exports.setStackPointer(stack_pointer);
		}

		if (doCommons)
		{
			const { buffer } = this.memory;

			this.doCommons(buffer);
		}

		if (this.exports.__getHeapSegments__)
		{
			this.__heap_segments = this.Size(this.exports.__getHeapSegments__(), 28);
			// this.__heap_segments = this.Size(this.exports.__heap_segments, 28);
		}

		if (this.exports._main)
		{
			this.exports._main();
		}

		if (code_type === __CODE_TYPE_BYTECODE__)
		{
			if (this.exports.__getAtomicThreadId__)
			{
				this.__lock__(this.exports.__getAtomicThreadId__());

				this.__id__ = this.Size(this.exports.__getThreadId__())[0]++;

				DEBUG_INFO_PUSH(`WASM THREAD: ${ this.__id__ }`);
				DEBUG_LABEL_LOG();

				this.__unlock__(this.exports.__getAtomicThreadId__());
			}
		}

		if (demangleCxxNames)
		{
			this.demangleCxxNames(debug);
		}

		// This is to be able to call .then()
		return (await new Promise(resolve => resolve(this)));
	}

	// Thread ()
	// {
	// 	return new WasmWrapper.Thread(this);
	// }

	// Thread2 ()
	// {
	// 	return new WasmWrapper.Thread2(this);
	// }

	// Thread3 ()
	// {
	// 	return new WasmWrapper.Thread3(this);
	// }

	async initThreads (thread_count)
	{
		const threads =
			new Array(thread_count)
				.fill(null)
				.map(() => new WasmWrapper.Thread3(this));

		await Promise.all(threads.map(thread => thread.init()));

		return threads;
	}
}



if (!self.window && self.SharedArrayBuffer && self.Atomics)
{
	const LOCKED = 0;
	const UNLOCKED = 1;

	WasmWrapper.prototype.__lock__ =
		function (addr)
		{
			// int32 size == 4
			const _index = addr / 4;

			while (true)
			{
				const old_state =
					Atomics.compareExchange
					(
						this.memory_views.I32,
						_index,
						UNLOCKED, // old state
						LOCKED, // new state
					);

				if (old_state === UNLOCKED)
				{
					return;
				}

				Atomics.wait(this.memory_views.I32, _index, LOCKED);
			}
		};

	WasmWrapper.prototype.__unlock__ =
		function (addr)
		{
			const _index = addr / 4;

			Atomics.store(this.memory_views.I32, _index, UNLOCKED);
			Atomics.notify(this.memory_views.I32, _index, 1);
		};
}
else
{
	WasmWrapper.prototype.__lock__ =
	WasmWrapper.prototype.__unlock__ =
		IDLE_FUNCTION;
}



export default WasmWrapper;
